import React, { useState, useEffect } from 'react';
import { Grid, Paper, TextField, Button, Typography, InputAdornment,
    IconButton, Input, InputLabel, FormControl, FormHelperText} from '@mui/material'
import { LockOutlined, Visibility, VisibilityOff } from "@mui/icons-material";
import { PulseLoader } from "react-spinners";
import { useNavigate } from 'react-router-dom';

import { postRequestHandler } from "./Requests";

const RegistrationPage = ({ setOpenAlert, setAlertType, setAlertMessage }) => {
    let navigate = useNavigate();

    //   
    const [firstName, setFirstName] = useState("");
    const [firstNameValidated, setFirstNameValidated] = useState(true);
    const [firstNameHelpText, setFirstNameHelpText] = useState(" ");

    const [secondName, setSecondName] = useState("");
    const [secondNameValidated, setSecondNameValidated] = useState(true);
    const [secondNameHelpText, setSecondNameHelpText] = useState(" ");

    const [lastName, setLastName] = useState("");
    const [lastNameValidated, setLastNameValidated] = useState(true);
    const [lastNameHelpText, setLastNameHelpText] = useState(" ");

    const [email, setEmail] = useState("");
    const [emailValidated, setEmailValidated] = useState(true);
    const [emailHelpText, setEmailHelpText] = useState(" ");

    const [password, setPassword] = useState("");
    const [passwordValidated, setPasswordValidated] = useState(true);
    const [passwordHelpText, setPasswordHelpText] = useState(" ");
    const [showPassword, setShowPassword] = useState(false);

    const [passwordCheck, setPasswordCheck] = useState("");
    const [passwordCheckValidated, setPasswordCheckValidated] = useState(true);
    const [showPasswordCheck, setShowPasswordCheck] = useState(false);

    const [phone, setPhone] = useState("");
    const [phoneValidated, setPhoneValidated] = useState(true);
    const [phoneHelpText, setPhoneHelpText] = useState(" ");

    //   
    const [registrationDisabled, setRegistrationDisabled] = useState(true);

    //   
    useEffect(() => {
        if (firstNameValidated && secondNameValidated && lastNameValidated &&
            emailValidated && passwordValidated && passwordCheckValidated && phoneValidated &&
            firstName !== "" && secondName !== "" && email !== "" && password !== "" &&
            passwordCheck !== "" && phone !== "") {
            setRegistrationDisabled(false);
        } else {
            setRegistrationDisabled(true);
        }
    }, [firstName, secondName, lastName, email, password, passwordCheck, phone]);

    const [isLoading, setIsLoading] = useState(false);

    //     
    const handleFirstNameChanged = (e) => {
        const val = e.target.value;
        const len = val.length;
        setFirstName(val);
        setFirstNameValidated(false);
        if (len < 1) {
            setFirstNameHelpText(" .");
        } else if (len > 32) {
            setFirstNameHelpText("    32 .");
        } else if (/\d/.test(val)) {
            setFirstNameHelpText("    .");
        } else if (val[len - 1] === ' ') {
            setFirstNameHelpText("      .");
        } else if (val[0] === ' ') {
            setFirstNameHelpText("      .");
        } else {
            setFirstNameValidated(true);
        }
    };

    //     
    const handleSecondNameChanged = (e) => {
        const val = e.target.value;
        const len = val.length;
        setSecondName(val);
        setSecondNameValidated(false);
        if (len < 1) {
            setSecondNameHelpText(" .");
        } else if (len > 32) {
            setSecondNameHelpText("    32 .");
        } else if (/\d/.test(val)) {
            setSecondNameHelpText("    .");
        } else if (val[len - 1] === ' ') {
            setSecondNameHelpText("      .");
        } else if (val[0] === ' ') {
            setSecondNameHelpText("      .");
        } else {
            setSecondNameValidated(true);
        }
    };

    //     
    const handleLastNameChanged = (e) => {
        const val = e.target.value;
        const len = val.length;
        setLastName(val);
        setLastNameValidated(false);
        if (len > 32) {
            setLastNameHelpText("    32 .");
        } else if (/\d/.test(val)) {
            setLastNameHelpText("    .");
        } else if (val[len - 1] === ' ') {
            setLastNameHelpText("      .");
        } else if (val[0] === ' ') {
            setLastNameHelpText("      .");
        } else {
            setLastNameValidated(true);
        }
    };

    //   email  
    const handleEmailChanged = (e) => {
        const val = e.target.value;
        const len = val.length;
        setEmail(val);
        setEmailValidated(false);
        if (val[len - 1] === ' ') {
            setEmailHelpText("      .");
        } else if (val[0] === ' ') {
            setEmailHelpText("      .");
        } else if (!/^[^@\s]+@[^@\s]+\.[^@\s]+$/.test(val)) {
            setEmailHelpText("  .");
        }
        else {
            setEmailValidated(true);
        }
    };

    //     
    const handlePasswordChanged = (e) => {
        const val = e.target.value;
        const len = val.length;
        setPassword(val);
        setPasswordValidated(false);
        if (len < 10) {
            setPasswordHelpText("    10 .");
        } else if (!/\d/.test(val)) {
            setPasswordHelpText("    .");
        } else if (val.toLowerCase() === val) {
            setPasswordHelpText("     .");
        } else {
            setPasswordValidated(true);
        }

        if (passwordCheck !== "") {
            if (val === passwordCheck) {
                setPasswordCheckValidated(true);
            } else {
                setPasswordCheckValidated(false);
            }
        }
    };

    //    
    const onShowPasswordClick = () => {
        setShowPassword(!showPassword);
    };
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    //      
    const handlePasswordCheckChanged = (e) => {
        const val = e.target.value;
        setPasswordCheck(val);
        val !== password? setPasswordCheckValidated(false): setPasswordCheckValidated(true);
    };

    //     
    const onShowPasswordCheckClick = () => {
        setShowPasswordCheck(!showPasswordCheck);
    };
    const handleMouseDownPasswordCheck = (event) => {
        event.preventDefault();
    };

    //     
    const handlePhoneChanged = (e) => {
        const val = e.target.value.replace(/\D/g,"");
        const len = val.length;
        setPhone(val);
        setPhoneValidated(false);
        if (len < 10 || len > 16) {
            setPhoneHelpText(" . : +79151234567");
        } else {
            setPhoneValidated(true);
        }
    };

    //   
    const onRegistrationClick = () => {
        setIsLoading(true);
        postRequestHandler('/api/v1/user/parent',
            {first_name: firstName,
                second_name: secondName,
                last_name: lastName,
                email: email,
                password: password,
                phone: phone})
            .then(response => {
            switch (response.status) {
                case 200:
                    navigate("/login?registered=true", { replace: true })
                    break;
                case 400:
                    setAlertType("info");
                    setAlertMessage("    .");
                    setOpenAlert(true);
                    break;
                case 409:
                    setRegistrationDisabled(true);
                    setEmailValidated(false);
                    setEmailHelpText("   .");
                    setAlertType("info");
                    setAlertMessage("   .");
                    setOpenAlert(true);
                    break;
                default:
                    setAlertType("error");
                    setAlertMessage("  .  .");
                    setOpenAlert(true);
            }
            setIsLoading(false);
        })
    }

    //     
    const onLoginClick = () => {
        navigate("/login", { replace: true })
    }

    const styles = {
        paperStyle: {
            padding: 20,
            borderRadius: 15,
            position: 'absolute',
            top: '50%',
            left: '50%',
            marginTop: -380,
            marginLeft: -160,
            width: 280,
            height: 720,
        },
        iconStyle: {
            color:'rgba(255,0,0,0.51)',
            width: 50,
            height: 50,
            padding:'10px'
        },
        labelStyle: {
            marginBottom:'20px'
        },
        textFieldStyle: {
            marginBottom:'0px'
        },
        buttonRegistrationStyle: {
            position: 'absolute',
            borderRadius: 15,
            width: 280,
            bottom:'75px',
            left: '20px',
        },
        buttonLoginStyle: {
            position: 'absolute',
            borderRadius: 15,
            width: 280,
            bottom:'20px',
            left: '20px',
        }
    };

    return(
        <Paper elevation={12} style={styles.paperStyle}>
            <Grid align='center'>
                <LockOutlined style={styles.iconStyle} />
                <Typography style={styles.labelStyle} variant="h5" gutterBottom>
                     
                </Typography>
            </Grid>

            <TextField
                style={styles.textFieldStyle}
                variant="standard"
                label=''
                fullWidth
                required
                onChange={handleFirstNameChanged}
                error={!firstNameValidated}
                helperText={firstNameValidated?" ":firstNameHelpText}
            />
            <TextField
                style={styles.textFieldStyle}
                variant="standard"
                label=''
                fullWidth
                required
                onChange={handleSecondNameChanged}
                error={!secondNameValidated}
                helperText={secondNameValidated?" ":secondNameHelpText}
            />
            <TextField
                style={styles.textFieldStyle}
                variant="standard"
                label=''
                fullWidth
                onChange={handleLastNameChanged}
                error={!lastNameValidated}
                helperText={lastNameValidated?" ":lastNameHelpText}
            />
            <TextField
                style={styles.textFieldStyle}
                variant="standard"
                type='email'
                label=' '
                fullWidth
                required
                onChange={handleEmailChanged}
                error={!emailValidated}
                helperText={emailValidated?" ":emailHelpText}
            />
            <FormControl fullWidth required error={!passwordValidated} variant="standard">
                <InputLabel htmlFor="register-password-text-field"></InputLabel>
                <Input
                    id="register-password-text-field"
                    style={styles.textFieldStyle}
                    onChange={handlePasswordChanged}
                    type={showPassword ? 'text' : 'password'}
                    endAdornment={
                        <InputAdornment position="end">
                            <IconButton
                                onClick={onShowPasswordClick}
                                onMouseDown={handleMouseDownPassword}
                            >
                                {showPassword ? <VisibilityOff /> : <Visibility />}
                            </IconButton>
                        </InputAdornment>
                    }
                />
                <FormHelperText id="register-password-text-field">{passwordValidated?" ":passwordHelpText}</FormHelperText>
            </FormControl>
            <FormControl fullWidth required error={!passwordCheckValidated} variant="standard">
                <InputLabel htmlFor="register-password-check-text-field"> </InputLabel>
                <Input
                    id="register-password-check-text-field"
                    style={styles.textFieldStyle}
                    onChange={handlePasswordCheckChanged}
                    type={showPasswordCheck ? 'text' : 'password'}
                    endAdornment={
                        <InputAdornment position="end">
                            <IconButton
                                onClick={onShowPasswordCheckClick}
                                onMouseDown={handleMouseDownPasswordCheck}
                            >
                                {showPasswordCheck ? <VisibilityOff /> : <Visibility />}
                            </IconButton>
                        </InputAdornment>
                    }
                />
                <FormHelperText id="register-password-check-text-field">{passwordCheckValidated?" ":"  ."}</FormHelperText>
            </FormControl>
            <TextField
                style={styles.textFieldStyle}
                variant="standard"
                label=' '
                fullWidth
                required
                onChange={handlePhoneChanged}
                error={!phoneValidated}
                helperText={phoneValidated?" ":phoneHelpText}
            />

            <Button
                style={styles.buttonRegistrationStyle}
                fullWidth
                onClick={onRegistrationClick}
                disabled={registrationDisabled || isLoading}
                color={'primary'}
                variant={'contained'}
            >
                {isLoading?
                    <div> <PulseLoader speedMultiplier={2} color={"#ffffff"} size={7} /></div>:
                    ""
                }
            </Button>

            <Button
                style={styles.buttonLoginStyle}
                fullWidth
                onClick={onLoginClick}
                color='primary'
                variant='outlined'
            >
                
            </Button>
        </Paper>
    )
};

export default RegistrationPage;